import React from "react";
import { AnimatePresence } from "framer-motion";
import { Route, Routes, useLocation } from "react-router-dom";
import Home from "../pages/home";
import Works from "../pages/works/works";
import AgnesPage from "../pages/projects/agnes/agnes_index";
import PostoPage from "../pages/projects/posto_index";
import EtiquetasPage from "../pages/projects/etiquetas_index";
import RepposPage from "../pages/projects/reppos_index";

const AnimatedRoutes = () => {
  const location = useLocation();
  return (
    <AnimatePresence exitBeforeEnter>
      <Routes location={location} key={location.pathname}>
        <Route path="/" element={<Home />} />
        <Route path="/works" element={<Works />} />
        <Route path="/works/agnes" element={<AgnesPage />} />
        <Route path="/works/posto" element={<PostoPage />} />
        <Route path="/works/etiquetas" element={<EtiquetasPage />} />
        <Route path="/works/reppos" element={<RepposPage />} />
      </Routes>
    </AnimatePresence>
  );
};

export default AnimatedRoutes;
